<template>
  <div class="content">
    <div style="display:none;">
      <button @click="test1">test</button>
      <button @click="test2">test2</button>
      <button @click="test3">test3</button>
      <button @click="test4()">test4</button>
      <button @click="test5()">test5</button>
    </div>
    <img class="btn-back" @click="goBack" src="../assets/back_off.png">
    <div class="qx-con" @click="ask">
      <div class="qx-box qx-box-top" ref="topBox"></div>
      <div class="qx-box qx-box-right" ref="rightBox"></div>
      <div class="qx-box qx-box-bottom" ref="bottomBox"></div>
      <div class="qx-box qx-box-left" ref="leftBox"></div>
    </div>
  </div>
</template>
<script>
// @ is an alias to /src
import QX from '@/components/qx.js';
import LanguageClass from "@/components/LanguageClass.js";
import fateData from "@/components/fateData.js";

let animate = require('gsap-promise');

export default {
  name: 'qxcontent',
  data(){
      return {
        //读取图片的对象,读取大部分图片都会用到它
        imgLoader: null,
        //镜面图
        defaultImg: "images/jm.png",
        //当前显示的mesh
        curMesh: null,
        //隐藏的mesh
        hiddenMesh: null,
        //镜子和各个镜面的组合对象
        mirrorGroup: null,
        //全息对象
        qx: null,
        //加载图片过程中的标记
        imageLoading: false,
        //变亮或者变暗的timer 目前为了简化功能,可能会去掉这个功能
        lightTimer: null,
        //是否开始占卜
        starting: false,
        //语音识别的文字
        recognizeText: "",
        //当前播放的声音的引用
        curSound: null,
        //是否在后台，距离传感器在后台也会触发
        onBackground: false,
        //开始占卜的声音
        zhanbuSound: require("../assets/zhanbu.mp3"),
      }
  },
  methods: {
    init() {
      this.qx = new QX({el: [this.$refs.topBox, this.$refs.rightBox, this.$refs.bottomBox, this.$refs.leftBox]});
      // o.addModel({modelName: "stickman", canRotate: true}).then(n=>{
      //   console.log("n ", n)
      //   this.t = n;
      // });
      this.addMainObj(10);
      this.addEvent();
      this.setScreenLight();
    },
    //设置屏幕亮度
    setScreenLight() {
      brightnessObj.setMaxBrightness();
    },
    //后退
    goBack() {
      this.$router.back();
    },    
    //添加事件
    addEvent() {
	     //距离感应
	    this.watchRef = proximityObj.startWatch((d)=>{
	      if(d < 5){
	        this.ask();
	      }
      }, ()=>{});
      this.onPauseFun = ()=> {
        this.onBackground = true;
      }
      this.onResumeFun = ()=> {
        this.onBackground = false;
      }
      this.onUnloadFun = ()=> {
        try {
          proximityObj.stopWatch(this.watchRef);
        } catch (e) {}
      }
      document.addEventListener('pause', this.onPauseFun, false);
      document.addEventListener('resume',this.onResumeFun, false);
      window.addEventListener("unload", this.onUnloadFun, false);             
    },

    //提问要算什么
    async ask() {
      if(this.onBackground) {
        return;      
      }
      if(this.starting) {
        return;
      }
      this.starting = true;
      this.showImage("images/eye.png");
      await this.speak("您有什么吩咐");
      this.listen();
    },
    //回答能算什么 然后问用户算什么
    async answerAndAsk() {
      await this.speak("我可以算今天的运气,和,生男生女, 您想算什么");
      this.listen();
    },
    //开始听
    listen() {
      speechObj.startRecognize((s)=>{
        //console.log(s);
        this.recognizeText = s;
        this.onLitened();
      });
    },
    //听完
    onLitened(){
      if(this.recognizeText == ""){
        this.stopZhanbu();
        return;
      }
      let text = this.recognizeText;
      this.recognizeText = "";  
      this.dealText(text);
    },
    //处理用户说的话
    async dealText(text) {
        //todo
        let result = LanguageClass.analyze(text);
        if(result == null) {
          await this.speak("对不起,我没听明白");
          this.listen();
          return;
        }
        if(result == LanguageClass.WHO_ARE_YOU) {
          await this.speak("我是工程师陈欣开发的魔镜app,我能预知未来");
          this.stopZhanbu_timeout();
        }
        else if(result == LanguageClass.ZHANBU_WHAT) {
          this.answerAndAsk();
        }
        //必须有的
        else if(result == LanguageClass.NICE) {
          await this.speak("当然是您了,我的主人");
          this.stopZhanbu_timeout();
        }
        //面试用
        else if(result == LanguageClass.INTERVIEW) {
          await this.speak("如果您的面试官,聪明,并且,颜值高,我想他一定会让您通过的,呵呵");
          this.stopZhanbu_timeout();
        }
        //生男生女
        else if(result == LanguageClass.BOY_GIRL) {
          this.zhanbu_boygirl();
        }
        //今天的运气
        else if(result == LanguageClass.FATE) {
          this.zhanbu_fate();
        }
    },
    //开始占卜男女
    async zhanbu_boygirl() {
      await this.speak("请在心中默念准妈妈的名字,并耐心等待结果");
      await this.startZhanbu();
      this.speak("请在镜子中查看结果");
      this.showImage(this.getBoygirl());
      this.stopZhanbu_timeout();
    },
    //获取男女
    getBoygirl() {
      let n = parseInt(Math.random() * 100);
      //如果是99,那就是多个
      if(n > 97){
        return "images/nn/m.png";
      }
      let r = n % 6;
      //console.log(n, r);
      return "images/nn/" + r + ".png";
    },
    async zhanbu_fate() {
      await this.speak("好的,请静下心来,耐心等待");
      await this.startZhanbu();
      let n = parseInt(Math.random() * fateData.length);
      this.speak("未来一天的运势, " + fateData[n][0]);
      this.showImage("images/fate/" + fateData[n][1] + ".png");
      this.stopZhanbu_timeout();
    },
    //说话
    speak(text) {
      let tk = this.$store.state.speechToken;
      let url = "http://tsn.baidu.com/text2audio?";
      let params= {
          tok: tk,
          lan: 'zh',
          tex: encodeURIComponent(encodeURIComponent(text)),
          cuid: "370189163",
          per: 2,
          ctp: 1,
      }
      url += Object.keys(params).map(k=>k + "=" + params[k]).join("&");
      // let arr = [];
      // for(let i in params) {
      //   arr.push(i + "=" + params[i]);
      // }               
      // url = url + arr.join("&");
      return this.playSound(url);
    },

    //播放声音
    playSound(url) {
      return new Promise((resolve)=> {
        this.curSound = new Audio();
        this.curSound.src = url;
        this.curSound.play(()=>{
          resolve();
        });
      })
    },

    //添加镜子
    async addMainObj(offsetY=0) {
      //  let obj = await this.qx.addObjectByRes({name: "mirror", texture: "skin.jpg", scale: 4});
      //  let handler = {
      //    id: "mirror",
      //    handle(){
      //      obj.rotation.y += .01;
      //    }
      //  }
      //  this.qx.addRenderListener(handler);
      //镜子整体
      let obj = await this.qx.loadObjectByRes({name: "mirror", texture: "skin.jpg", scale: 5});
      //镜面
      let geometry = new THREE.PlaneGeometry(50,60);
      let loader = new THREE.TextureLoader();
      let texture = new THREE.Texture();
      this.imgLoader = new THREE.ImageLoader();
      this.imgLoader.load(this.defaultImg, (img)=> {
          texture.image = img;
          texture.needsUpdate = true;
      });
      let material = new THREE.MeshLambertMaterial({map: texture,transparent:true});
      let photo = new THREE.Mesh(geometry,material);
      photo.position.y = -10;
      photo.position.z = 0;
      this.curMesh = photo;
      //photo.material.map = texture;
      //把镜子整体和镜面组合 然后加到场景里面
      let gp = new THREE.Group();
      gp.position.y = offsetY;
      gp.add(obj);
      gp.add(photo);
      this.mirrorGroup = gp;

      this.qx.addObject(gp);
      //再创建一个镜面 隐藏起来
      let geometry_se = new THREE.PlaneGeometry(50,60);
      let texture_se = new THREE.Texture();
      let material_se= new THREE.MeshLambertMaterial({map: texture_se, transparent:true, opacity: 0});
      let photo_se = new THREE.Mesh(geometry_se, material_se);
      photo_se.position.y = -10;
      photo_se.position.z = 0;
      this.qx.addObject(photo_se, {disableAnimate: true, parent: gp});
      this.hiddenMesh = photo_se;

      let handler = {
        id: "mirror",
        handle(){
          //gp.rotation.y += .01;
        }
      }
      this.qx.addRenderListener(handler);

    },
    //六芒星
    addLMX() {
      let geometry = new THREE.PlaneGeometry(150,150);
      let texture = new THREE.Texture();
      this.imgLoader.load("images/lmx.png", (img)=> {
          texture.image = img;
          texture.needsUpdate = true;
      });
      let material = new THREE.MeshLambertMaterial({map: texture,transparent:true});
      let lmx = new THREE.Mesh(geometry,material);
      lmx.material.side = THREE.DoubleSide;
      lmx.position.z = -20;
      this.qx.addObject(lmx);
      let handler = {
        id: "lmx",
        handle(){
          lmx.rotation.z += .01;
        }
      }
      this.qx.addRenderListener(handler); 
      this.lmx = lmx;     
    },
    //删除六芒星
    removeLMX(){
      if(this.lmx){
        this.qx.removeObject(this.lmx);
        this.qx.removeRenderListenerById("lmx");
        this.lmx = null;
      }
    },
    //阳光
    addSunLight() {
      // let spriteMap = new THREE.TextureLoader().load( 'images/lensflare0.png' );
      // let spriteMaterial = new THREE.SpriteMaterial( { map: spriteMap, color: 0xffffff } );
      // let sprite = new THREE.Sprite( spriteMaterial );
      // sprite.scale.set(512, 512, 1);
      // //sprite.position.z = 10;
      // this.qx.addObject(sprite)

      let geometry = new THREE.PlaneGeometry(500,500);
      let texture = new THREE.Texture();
      this.imgLoader.load("images/sun.png", (img)=> {
          texture.image = img;
          texture.needsUpdate = true;
      });
      let material = new THREE.MeshLambertMaterial({map: texture,transparent:true});
      let sun = new THREE.Mesh(geometry,material);
      sun.material.side = THREE.DoubleSide;
      sun.position.z = 3;
      this.qx.addObject(sun);  
      this.sunLight = sun;
    },
    //删除阳光
    removeSunLight() {
      if(this.sunLight){
        this.qx.removeObject(this.sunLight);
        this.sunLight = null;
      }
    },
    //显示图片 先把之前的隐藏,再显示另外一个,有两mesh,交换隐藏显示
    showImage(imgSrc) {
      if(this.imageLoading){
        console.log("loading");
        return;
      }
      this.imageLoading = true;
      return new Promise((resolve)=>{
        this.imgLoader.load(imgSrc, async (img)=>{
          //console.log(this.hiddenMesh.material.map)
          this.hiddenMesh.material.map.image = img;
          this.hiddenMesh.material.map.needsUpdate = true;
          await Promise.all([...this.qx.showObject(this.hiddenMesh), ...this.qx.hideObject(this.curMesh)]);
          [this.curMesh, this.hiddenMesh] = [this.hiddenMesh, this.curMesh];
          //console.log("set false")
          this.imageLoading = false;
          resolve();
        })
      })
    },
    //显示默认的图片
    showDefaultImage() {
      return this.showImage(this.defaultImg);
    },
    test1() {
      //this.qx.removeRenderListenerById("mirror");
      //this.showImage("images/eye.png");
      this.recognizeText = "你能算什么";
      this.onLitened();
    },
    test2() {
      //this.qx.removeRenderListenerById("mirror");
      //this.showDefaultImage();
      this.recognizeText = "我能通过面试吗";
      this.onLitened();      
    },
    test3() {
      //this.qx.scene.remove(this.mirrorGroup);
      this.recognizeText = "今天的运气";
      this.onLitened();      
    },
    test4() {
      //this.qx.scene.remove(this.mirrorGroup);
      this.recognizeText = "生男生女";
      this.onLitened();      
    },
    test5() {
      //this.qx.scene.remove(this.mirrorGroup);
      this.recognizeText = "这个世界上谁最英俊";
      this.onLitened();      
    },
    //开始占卜
    startZhanbu() {
      return new Promise(async (resolve)=>{
        this.addLMX();
        await this.showDefaultImage();
        this.playSound(this.zhanbuSound);
        await animate(this.mirrorGroup.rotation, 6, { y: THREE.Math.degToRad(2880) });
        this.mirrorGroup.rotation.y = THREE.Math.degToRad(0);
        this.addSunLight();
        setTimeout(()=>{
          this.removeLMX()
          this.removeSunLight();
          resolve();
        }, 9000);          
      })
    },
    //停止占卜
    stopZhanbu() {
      this.showDefaultImage();
      this.removeLMX();
      this.removeSunLight(); 
      speechObj.stopRecognize();
      try{
        if(this.curSound){
          this.curSound.stop();
        }
      }catch(e){}
      this.starting = false;
    },
    //延时停止占卜
    stopZhanbu_timeout() {
      setTimeout(()=>{
        this.stopZhanbu();
      }, 8000);      
    },
    //变亮
    onBrighten(){
      if(this.lightTimer){
        clearTimeout(this.lightTimer);
        this.lightTimer = null;
      }
      this.qx.brighten();
    },
    //变暗
    onDarken(){
      this.lightTimer = setTimeout(()=>{
        this.qx.darken();
      }, 5000);
    }
    
  },
  mounted() {
    this.init();
  },
  beforeDestroy() {
    document.removeEventListener('pause', this.onPauseFun, false);
    document.removeEventListener('resume',this.onResumeFun, false);
    window.removeEventListener("unload", this.onUnloadFun, false);     
    this.stopZhanbu();
    proximityObj.stopWatch(this.watchRef);
    this.qx.destroy();
    brightnessObj.restoreBrightness();
  }
}
</script>
<style scoped lang="scss">
  .content{
    background: #000000;
    height: 100%;
  }
  .qx-con{
    width: 100vw;
    height: 100vw;
    position: fixed;
    top: 50%;
    left: 50%;
    transform: translate(-50%, -50%);
    .qx-box.qx-box-top{
      width: 100%;
      height: 50%;
      position: absolute;
      top: 0;
      left: 0;
      -webkit-clip-path: polygon(100% 0%, 50% 100%, 0% 0%);
      clip-path: polygon(100% 0%, 50% 100%, 0% 0%);
    }
    .qx-box.qx-box-right{
      width: 50%;
      height: 100%;
      position: absolute;
      top: 0;
      right: 0;
      -webkit-clip-path: polygon(100% 0%, 100% 100%, 0% 50%);
      clip-path: polygon(100% 0%, 100% 100%, 0% 50%);
    }
    .qx-box.qx-box-bottom{
      width: 100%;
      height: 50%;
      position: absolute;
      top: 50%;
      left: 0;
      -webkit-clip-path: polygon(50% 0%, 100% 100%, 0% 100%);
      clip-path: polygon(50% 0%, 100% 100%, 0% 100%);
    }
    .qx-box.qx-box-left{
      width: 50%;
      height: 100%;
      position: absolute;
      top: 0;
      left: 0;
      -webkit-clip-path: polygon(100% 50%, 0% 100%, 0% 0%);
      clip-path: polygon(100% 50%, 0% 100%, 0% 0%);
    }
  }
</style>
