export default class LanguageClass {
    static WHO_ARE_YOU = Symbol();
    static ZHANBU_WHAT = Symbol();
    static INTERVIEW = Symbol();
    static BOY_GIRL = Symbol();
    static FATE = Symbol();
    static NICE = Symbol();
    static analyze(txt) {
      if(txt == "" || /不[想要]/.test(txt)){
        return null;
      }
      let analyzeDatalist = new Map([
        [/^[你您]*是谁[啊呀]*$/, LanguageClass.WHO_ARE_YOU],
        [/^[你您]*[都]*[会能]*[算预测]*什么[啊呀]*$/, LanguageClass.ZHANBU_WHAT],
        [/^[您你请能帮我想要算一算我今天]*能[不能]*通过面试[吗]*$/, LanguageClass.INTERVIEW],
        [/^[您你请能帮我想要算一算我]*今天的面试能[不能]*通过[吗]*$/, LanguageClass.INTERVIEW],
        [/^[您你请能帮我想要帮给我(\S{1,4})算一算她]*生[的是]*男[娃孩儿还是生]*女[孩儿娃吗]*$/, LanguageClass.BOY_GIRL],
        [/^[您你请能帮我想要算一算我(\S{1,4})]*生[的是]*男[娃孩儿还是生]*女[孩儿娃吗]*$/, LanguageClass.BOY_GIRL],
        [/^[您你请能帮我想要算一算我(\S{1,4})]*今天的运气[如何怎么样咋吗]*$/, LanguageClass.FATE],
        [/^[镜子魔镜啊]*[请问这个]*世界上谁最[漂亮美丽英俊帅]*$/, LanguageClass.NICE]
      ]);
      for (let [key, value] of analyzeDatalist) {
        if(key.test(txt)){
          return value;
        }
      }
      return null;
    }
}
  